import { mapState, mapActions, mapMutations} from 'vuex'
import { updateUser } from '@/api/user'

export default {
  middleware: 'authenticated',
  name: "SettingsIndex",

  data() {
    return {
      profile: {
        image: '',
        username: '',
        bio:'',
        email: '',
        password: ''
      },
      updating: false
    }
  },

  computed: {
    ...mapState(['user'])
  },

  mounted () {
    // 获取vuex中的用户信息
    Object.keys(this.profile).forEach(key  => {
      if (this.user[key]) {
        this.profile[key] = this.user[key]
      }
    })
  },

  methods: {
    ...mapActions(['updateUserState']),
    ...mapMutations(['cleanUser']),

    async updateProfile () {
     this.updating = true

     let user = {}
     Object.keys(this.profile).forEach(key => {
       if (this.profile[key] !== '') {
         user[key] = this.profile[key] 
       }
     })

     await this.updateUserState(user)
     this.updating = false
      this.$router.push({
        name: 'profile',
        params: {
          username: this.user.username
        }
      })
    },

    logout () {
      this.cleanUser()
      this.$router.push({
        name: 'home'
      })
    }
  }
}
